@import 'utils';
@import 'utils.scss';
@import 'ScssSection.scss';
.ScssComponent {
    display: flex;
    background: red;
    background: $oc-gray-2;
    @include media('<768px') {
        background: $oc-gray-9;
    }
    .box {
        cursor: pointer;
        transition: all 0.3s ease-in;
        &.red {
            // red클래스가  .box와 함께 사용되었을 때
            background: $red;
            @include square(1);
        }
        &.orange {
            background: $orange;
            @include square(2);
        }
        &.yellow {
            background: $yellow;
            @include square(3);
        }
        &.green {
            background: $green;
            @include square(4);
        }
        &.blue {
            background: $blue;
            @include square(5);
        }
        &.indigo {
            background: $indigo;
            @include square(6);
        }
        &.violet {
            background: $violet;
            @include square(7);
        }
        &:hover {
            background: #333333;
        }
    }
}
